body {
  margin: 0;
  padding: 0;
}

.people {
  color: #333;
  grid-template-columns: 1fr 1fr;
  gap: 10px;
  min-width: 500px;
  font-family: Arial, Helvetica, sans-serif;
  display: grid;
}

@media (width >= 1000px) {
  .people {
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }
}

.person {
  flex-direction: column;
  align-items: center;
  width: 200px;
  height: 231px;
  margin: 35px;
  display: flex;
}

.photo img {
  border-radius: 50%;
  width: 200px;
  height: 200px;
  position: relative;
}

.name {
  text-align: center;
  margin-top: 10px;
  font-size: 16px;
}

.extra {
  opacity: 0;
}

.person:hover {
  transition-duration: 1s;
  scale: 1.3;
}

.person:hover .photo img {
  border-radius: 20%;
}

.person:hover .extra {
  opacity: 1;
}

.rank {
  background-color: #f0f8ff;
  border-radius: 50%;
  justify-content: center;
  width: 20px;
  height: 20px;
  display: flex;
  position: absolute;
  top: 40%;
  right: 20%;
}
